<script>
export default {
  name: 'Result',
  props: {
    msg: String
  },
   data: function(){
    return {
      answers: [],
      fixedScore: 0,
      growthScore: 0,
      counter: 45,
      mayor: 0,
      max: 40
    }
   },
   methods: {
     onPrint(){
       console.log('print');
       window.print();
     },

     calculate(){
       console.log('print');
       for(var i = 1; i <= this.answers.length; i++){
         console.log(this.answers[i-1]);
         if(i == 1 || i == 4 || i == 5 || i == 8 || i == 9 ){
           this.fixedScore += this.answers[i-1].answer;
         }else{
           this.growthScore += this.answers[i-1].answer;
         }
       }

       if(this.growthScore > this.fixedScore){
         this.mayor = this.growthScore;
       }else{
         this.mayor = this.fixedScore;
       }
     }

   },
  mounted() {
  console.log('App mounted!');
    if (localStorage.getItem('answers')) 
    this.answers = JSON.parse(localStorage.getItem('answers'));
    this.calculate();
  },
}
</script>

