<template>
  <div class="container-fluid">
    <div class="header">
      <h3>Thrive Through Change Growth Mindset Quiz</h3>
    </div>

    <div class="container box">
      
          <div class="row">
            <div class="col">{{index}}/10</div>
            <div class="col"><h4>Question {{index}}</h4></div>
            <div class="col"> 
              <button type="button" class="btn btn-default">
                <img height="20px" width="20px" src="@/assets/icons8-cancel-500.png"/>
              </button>
            </div>
          </div>
          <div><p>{{ questions[index-1].question }}</p></div>
  
          <div> 
            <MyLottie :valor="myValue" v-on:answer="getAnswer" v-on:next="onNext"/>
          </div>
          <br>
          

          
    </div>
    </div>

</template>


<script>
import MyLottie from '@/components/MyLottie.vue'
export default {
  name: 'Question',
  components: {
    MyLottie
  },
  props: {
    msg: String,
  },
  data: function(){
    return {
    myValue:0,
    index: 2,
    answers: [],
    questions: [
        {
          id: 1,
          animation: 1,
          question: 'I try to outperform my coworkers',
        },
        {
          id: 2,
          animation: 2,
          question: 'I like having colleagues who can teach me something about myself, even if it isn’t always positive',
        },
        {
          id: 3,
          animation: 3,
          question: 'I’m always seeking opportunities to develop new skills and acquire new knowledge',
        },
        {
          id: 4,
          animation: 4,
          question: 'I really care about making a good impression on other people',
        },
        {
          id: 5,
          animation: 5,
          question: 'It’s important to me to show that I am smart and capable',
        },
        {
          id: 6,
          animation: 6,
          question: 'I strive to have open and honest relationships with my coworkers.',
        },
        {
          id: 7,
          animation: 7,
          question: 'I work to constantly learn and improve at my job.',
        },
        {
          id: 8,
          animation: 8,
          question: 'When I’m with my coworkers, I think a lot about how I’m “coming across” to them',
        },
        {
          id: 9,
          animation: 9,
          question: 'I feel good about myself when I know that my colleagues like or are impressed by me.',
        },
        {
          id: 10,
          animation: 10,
          question: 'I like to work with, and for, people who challenge me to change for the better.',
        }
      ]
    }
  },
  mounted() {
  console.log('App mounted!');
    if (localStorage.getItem('answers')) 
    this.answers = JSON.parse(localStorage.getItem('answers'));
  },
  watch: {
    answers: {
      handler() {
        console.log('Todos changed!');
        localStorage.setItem('answers', JSON.stringify(this.answers));
      },
      deep: true,
    },
  },
  methods: {
     onNext(next) {
       console.log('next');
       this.myValue = 1;
       this.myValue = 0;
       this.index++;
       if(this.index === 11){
         this.$router.push({ name: 'result'});
       }else{
          this.$router.push({ name: 'question', params: { num: this.index }});
       }
    },
    getAnswer(answer){
      this.answers[this.index-1].answer = parseInt(answer,10)+1;
      //localStorage.setItem('answers', JSON.stringify(this.answers));
      console.log('answer', answer);
    }
  }
}
</script>

